import { useState} from 'react';
import photo from "./images/pic.jpg";
import './App.css';
import Display from "./Component/Display";





function App() {

    //const [state, setState] = useState(initialState)

    const[skills, setSkills] = useState("My Name is Chris")

    const handleSkills =()=>{
      setSkills("I'm a Frontend Developer")
    }
    const handleName =()=>{
      setSkills("My Name is Chris")
    }


    const [count, setCount] = useState(0)
  function incrementCount(){
    setCount(prevCount => prevCount + 1)
  }


  return (
    <>
    <main>
        <div className="appstyle">
          <img src={photo} alt="red" className="photo"/>
          <h1>Hi!</h1>


      <Display tech ={skills}/>

      <div className="btns">
        


          <button onClick={handleName}>Name</button>
          <button onClick={handleSkills}>Skills</button>    
          <br/><span>{count}</span><i class="fas fa-heart" onClick={incrementCount}></i>
          <br/><a href="https://twitter.com/home"><i class="fab fa-twitter"></i></a>

          
      </div>

              </div>


              
          
              

              </main>
    </>
  );
}

export default App;
